.hero {
  min-height: 773px;
  background:
    linear-gradient(180deg, rgba(25, 32, 36, 0.7) 71.72%, rgba(25, 32, 36, 0) 100%),
    url("../img/background.jpg") no-repeat 0 60%;
  background-size: cover;
  background-color: var(--themed-dark);

  @include vp-1023 {
    min-height: 547px;
    background-position: 18.5% 62%;
    background-size: 178.8%;
  }

  @include vp-767 {
    min-height: 493px;
    background-position: 25.5% 60%;
    background-size: 386%;
  }
}

.hero__container {
  grid-column: main;
  display: grid;
  place-items: start;
  grid-template-columns: repeat(2, minmax(0, 1fr));
  grid-auto-rows: min-content;
  column-gap: 20px;
  padding: 198px 126px 160px;

  @include vp-1023 {
    padding: 87px 0;
  }

  @include vp-767 {
    padding: 84px 0;
  }

  h2 {
    grid-column: span 2;
    margin: 0;
    margin-bottom: 31px;
    padding: 0;
    font: 700 80px / 94px $ff-headings;
    letter-spacing: 0.07em;
    text-transform: uppercase;
    color: var(--base-light);

    @include vp-1023 {
      margin-bottom: 0;
      font-size: 64px;
      line-height: 75px;
    }

    @include vp-767 {
      margin-bottom: 7px;
      font-size: 36px;
      line-height: 42px;
    }
  }

  p {
    grid-column: span 2;
    max-width: 52ch;
    margin: 0;
    margin-bottom: 48px;
    padding: 0;
    font: 400 22px / 34px $ff-main;
    color: var(--base-light);

      @include vp-1023 {
        margin-bottom: 57px;
      }

      @include vp-767 {
        margin-bottom: 44px;
        font-size: 14px;
        line-height: 22px;
      }
  }

  a {
    --color: var(--base-light);
    --bg-color: var(--themed-light);
    --border-color: var(--themed-light);

    display: inline-flex;
    min-width: 10ch;
    max-width: 47ch;
    margin-bottom: 30px;
    padding: 17px 26px;
    font: 400 18px / 21px $ff-headings;
    letter-spacing: 0.06em;
    overflow: hidden;
    text-overflow: ellipsis;
    text-transform: uppercase;
    text-align: left;
    color: var(--color);
    background-color: var(--bg-color);
    border: 1px solid var(--border-color);
    border-radius: 3px;
    user-select: none;
    cursor: pointer;

    @include vp-1023 {
      grid-column: span 2;
      margin-left: 45px;
    }

    @include vp-767 {
      min-width: 100%;
      max-width: 100%;
      justify-content: center;
      margin-left: 0;
      padding: 12px 30px;
      font-size: 0;
      line-height: 0;
    }

    @include hover {
      --color: var(--themed-light);
      --bg-color: var(--base-light);
    }

    @include focus {
      --color: var(--themed-light);
      --bg-color: var(--base-light);
    }

    @include active {
      --color: var(--themed-dark);
      --border-color: var(--themed-dark);
    }
  }

  span {
    position: absolute;
    font-size: 0;

    @include vp-767 {
      position: static;
      font-size: 14px;
      line-height: 16px;
      letter-spacing: 0.05em;
    }
  }
}
